
import { doAjax } from "@/utils/ajax";
import { ElMessageBox } from "element-plus";
import { Options, Vue } from "vue-class-component";
import moment from "moment";
@Options({
  components: {},
})
export default class Room extends Vue {
  flagLoading = false;
  listData: any = [];
  lastForm: any = {};
  form = {
    tag: 30000,
    date: "2022-04-11 14:30:00",
    timeType: "days",
  };
  async getList() {
    this.flagLoading = true;
    this.lastForm = Object.assign({}, this.form);
    let res: any = await doAjax({
      url: "/asset/rank",
      method: "post",
      data: this.form,
    });
    this.flagLoading = false;
    this.listData = res;
  }
  count = 0;
  async getCountOnline() {
    let num: any = await doAjax({
      url: "/user/online/count",
      method: "get",
    });
    this.count = num;
  }
  getGold(d: any) {
    if (!d) {
      return {};
    }
    let res = Object.assign({}, d);
    if (res["counter_data"]) {
      res = res["counter_data"];
    }
    if (res["" + this.lastForm.tag]) {
      res = res["" + this.lastForm.tag];
    }
    if (res[this.lastForm.timeType]) {
      res = res[this.lastForm.timeType];
    }
    let dd = {};
    for (let key in res) {
      dd = res[key];
    }
    return dd;
  }
  listOption: any = [];
  mounted() {
    let map: any = {
      10000: "游戏赢取",
      20000: "游戏失败",
      30000: "系统回收",
      40000: "礼物赠送",
      50000: "消费",
      60000: "活动领取",
      0: "其它",
    };
    for (let id in map) {
      this.listOption.push({
        label: map[id],
        value: +id,
      });
    }
    this.form.date = moment().format("YYYY-MM-DD HH:mm:ss");
    this.getCountOnline();
  }
}
